<template>
    <a-form-model ref="akForm" :model="form" :rules="rules" v-bind="formItemLayout">
        <a-form-model-item label="开启调试" prop="debug">
            <a-radio-group v-model="form.debug" @change="onChange">
                <a-radio value="1">开启</a-radio>
                <a-radio value="0">关闭</a-radio>
            </a-radio-group>
        </a-form-model-item>
        <a-form-model-item label="网关服务api地址" prop="api_url">
            <a-input placeholder="以'http://'或'https://'头" v-model="form.api_url" />
        </a-form-model-item>
        <a-form-model-item label="ACCESS KEY" prop="access_key" help="请保持与网关配置一致">
            <a-input placeholder="ACCESS KEY" v-model="form.access_key" />
        </a-form-model-item>
        <a-form-model-item label="流媒体代理配置" prop="media_server_id">
            <a-select placeholder="请选择服务节点" v-model="media_server_id" @change="onMediaServerChange">
                <a-select-option value="">请选择</a-select-option>
                <a-select-option v-for="(node, key) in mediaServerItems" :value="node" :key="key">{{
                    node
                }}</a-select-option>
            </a-select>
        </a-form-model-item>
        <a-form-model-item label="代理地址" v-show="show_proxy_input">
            <a-input placeholder="以'http://'或'https://'头" v-model="proxy_url" />
        </a-form-model-item>
        <a-form-model-item :wrapper-col="{ span: 12, offset: 6 }">
            <a-button type="primary" @click="onSubmit">保存</a-button>
        </a-form-model-item>
    </a-form-model>
</template>

<script>
import { SYSTEM_VALIDATE_RULE_PATTERNS } from '@/libs/constants'
import { getMediaServerList } from '@/api/device'
import { getAkServer, editAkServer } from '@/api/setting'
const validateUrl = (rule, value, callback) => {
    if (value === '') {
        callback(new Error('网关服务api地址必须填写'))
    } else if (!SYSTEM_VALIDATE_RULE_PATTERNS.URL.test(value)) {
        callback(new Error('网关服务api地址不正确'))
    }
    callback()
}
export default {
    data: () => ({
        form: {
            api_url: '',
            access_key: '',
            media_server_proxy: {},
            debug: '0'
        },
        show_proxy_input: false,
        proxy_url: '',
        media_server_id: '',
        mediaServerItems: [],
        formItemLayout: {
            labelCol: { span: 6 },
            wrapperCol: { span: 14 }
        },
        rules: {
            access_key: [{ required: true, message: 'AK_ACCESS_KEY必须填写', trigger: 'blur' }],
            api_url: [
                { required: true, message: '网关服务api地址必须填写', trigger: 'blur' }
                // { validator: validateUrl, trigger: 'blur' }
            ]
        }
    }),
    mounted() {
        this.loadmediaServerItems()
        this.getAkServerValue()
    },
    methods: {
        async loadmediaServerItems() {
            this.mediaServerItems = await getMediaServerList()
        },
        async getAkServerValue() {
            const value = await getAkServer()
            this.form = value
            if(!this.form.hasOwnProperty('media_server_proxy')) {
                this.form.media_server_proxy = {}
            }
        },
        onSubmit() {
            this.$refs.akForm.validate((valid) => {
                if (!valid) {
                    return false
                }

                if (this.proxy_url) {
                    if (!SYSTEM_VALIDATE_RULE_PATTERNS.URL.test(this.proxy_url)) {
                        this.$message.error('请输入正确的代理地址');
                        return false;
                    }
                    this.form.media_server_proxy[this.media_server_id] = this.proxy_url
                    this.proxy_url = ''
                }

                editAkServer(this.form).then((response) => {
                    this.$message.success('保存成功')
                })
            })
        },
        onChange(e) {
            this.form.debug = e.target.value
        },
        onMediaServerChange(e) {
            if (this.media_server_id) {
                this.show_proxy_input = true
                this.proxy_url = this.form.media_server_proxy[this.media_server_id] ? this.form.media_server_proxy[this.media_server_id] : ''
            } else {
                this.show_proxy_input = false
            }
        }
    }
}
</script>
<style>
#components-form-demo-validate-other .dropbox {
    height: 180px;
    line-height: 1.5;
}
</style>
